<template>
  <div class="lf-box">
    <!-- :style="{width: htmlWidth - 60 * htmlProportion + 'px', height: htmlProportion * 500 + 'px'}" -->
    <div class="lf-content-payPanel">
      <div>
        <div class="one">
          <span class="">付款给&nbsp;<span v-text="this.merchantName"></span></span>
          <div>
            <img v-if="this.merchantImg" :src="merchantImg" class="headSculpture" alt="">
            <img :src="headSculpture" class="headSculpture" alt="">
            </div>
        </div>
      </div>
    </div>
    <!-- 下半部分 -->
    <div class="bottom">
      <!-- 金额 -->
      <div class="jiner">
        <div class="text">金额</div>
        <div class="money" :class="{money_shuru:money_shuru}">
          <span class="money_fuhao">{{yenSymbol}}</span>
          <span v-if="isAllowModifyAmount" class="text-lg pay-price-input" v-text="keyboardOption.value" @click="showKeyboard"
            ref="keyboard"></span>
          <span v-else class="text-lg pay-price-input" v-text="keyboardOption.value"></span>
          <Keyboard :keyboardOption="keyboardOption" :toPayFunc="toPayFunc"></Keyboard>
        </div>
      </div>
      <!-- 优惠 -->
      <div class="youhui">
        <div class="text">优惠</div>
        <div class="two" @click="showCoupon">
          <!-- <span class="text-blue iconfont  lf-icon-youhuiquan"></span> -->
          <div class="youhui-text">
            <span class="daijinquan">代金券</span>
            <span class="daijin-con">
              <span v-if="selectedCoupon">{{selectedCoupon.couponName}}</span>
              <span v-else>{{ canUseCouponList.length ? canUseCouponList.length+'张可用优惠券': couponList.length ? '有'+couponList.length+'张优惠券':'暂无可用'}}</span>
              <span class="ringt1"><img src="../../../assets/right1.svg" alt=""></span>
            </span>
          </div>
        </div>
      </div>
      <!-- 支付方式 -->
      <div class="zhifu">
        <div class="zhifu-title">
          <span class="text">支付方式</span>
        </div>
        <!-- 是会员 -->
        <div class="lf-content-payWay margin-0-auto" v-if="isMember">
          <div>
            <div>
              <span class="text-2dc26f iconfont lf-icon-weixinzhifu margin-right-10"></span>
              <span class="text-sm">微信支付</span>
            </div>
            <span v-if="isWxPay" class="orange iconfont lf-icon-icon-radio" :class="{'lf-icon-icon-radio1':isMemberRadio}"
              @click="changeMemberPayWay(true)"></span>
            <span v-else class="iconfont lf-icon-icon-radio1"></span>
          </div>
          <div>
            <div>
              <span class="text-yello2 iconfont lf-icon-huiyuanqia-copy margin-right-10"></span>
              <span class="text-sm">会员储值卡({{yenSymbol}}{{memberBalance / 100}})</span>
            </div>
            <span v-if="isWxPay" class="orange iconfont lf-icon-icon-radio" :class="{'lf-icon-icon-radio1':!isMemberRadio}"
              @click="changeMemberPayWay(false)"></span>
            <span v-else class="iconfont lf-icon-icon-radio1"></span>
          </div>
        </div>
        <!-- 不是会员 -->
        <div v-else class="lf-content-payWay margin-0-auto">
          <div>
            <div>
              <span class="text-2dc26f iconfont lf-icon-weixinzhifu margin-right-10"></span>
              <span>微信支付</span>
            </div>

            <span v-if="isWxPay" class="orange iconfont lf-icon-icon-radio"></span>
            <span v-else class="iconfont lf-icon-icon-radio1"></span>
          </div>
          <div class="join_vip" @click="jumpAddMember">
            <div>
              <span class="text-white iconfont lf-icon-huiyuanqia-copy margin-right-10"></span>
              <span>加入会员</span>
             </div>
             <div><img src="../../../assets/arrow-icon.svg" alt="" class="join_img"></div>

          </div>
        </div>
      </div>
      <div class="fukuan">
        <div class="fukuan_text" @click="payConfirmModal"><span class="zhifu-text1">付款</span><span class="zhifu-fuhao">¥</span>{{actualAmount / 100 }}</div>
      </div>

    </div>
    <!-- 优惠券选取 -->
    <transition name="couponSelect">
      <div v-if="isShowCoupon">
        <div class="lf-cover" @click="closeCoupon"></div>
        <div class="lf-coupon-modal padding" style="width: 100%;">
          <p class="confirm"><span class="text-sm">选择优惠券</span><span class="text-ssm btn" @click="closeCoupon">确定</span></p>
          <div class="lf-no-coupon" @click="changeSelection(-1)">
            <span class="text-ssm text-weight">不使用优惠券</span>
            <img src="../../../assets/selection_orange.png" v-if="isShowSelection">
            <img src="../../../assets/selection_grey.png" v-else>
          </div>
          <div class="lf-coupon-box">
            <!-- 可用优惠券 -->
            <div class="lf-count-canCoupon" v-if="canUseCouponList.length">
              <img class="img" src="../../../assets/line_452.png">
              <span class="text-sm">可用优惠券({{canUseCouponList.length}}张)</span>
              <img class="img" src="../../../assets/line_452.png">
            </div>
            <div class="lf-coupon-card coupon-bg-01 relative" v-for="(card,key) in canUseCouponList" :key="key">
              <div class="one">
                <div class="up">
                  <img src="../../../assets/icon_member_default_coupon.png">
                  <div>
                    <span class="text-weight">{{card.couponName}}</span>
                    <span class=" text-orange">
                      <span class="text-weight">{{yenSymbol}}{{card.couponAmount / 100}}</span>
                      <span class="text-ssm lf-coupon">{{card.payAmountLimit == 0 ? '满任意金额可用':'满'+card.payAmountLimit/100 +'金额可用'}}</span>
                    </span>
                  </div>
                </div>
              </div>
              <div class="two" @click="changeSelection(key)">
                <img src="../../../assets/selection_orange.png" v-if="card.selectStatus">
                <img src="../../../assets/circular_orange.png" v-else>
              </div>
            </div>
            <!-- 不可用优惠券 -->
            <div class="lf-count-canCoupon" v-if="canNotUseCouponList.length">
              <img class="img" src="../../../assets/line_452.png">
              <span class="text-sm">不可用优惠券({{canNotUseCouponList.length}}张)</span>
              <img class="img" src="../../../assets/line_452.png">
            </div>
            <div class="lf-coupon-card coupon-bg-02 relative" v-for="(card,key) in canNotUseCouponList" >
              <div class="one">
                <div class="up">
                  <img src="../../../assets/icon_member_default_coupon.png">
                  <div>
                    <span class="text-weight">{{card.couponName}}</span>
                    <span class=" text-grey">
                      <span class="text-weight">{{yenSymbol}}{{card.couponAmount /100 }}</span>
                      <span class="text-ssm lf-coupon-no">{{card.payAmountLimit == 0 ? '满任意金额可用':'满'+card.payAmountLimit /100 +'金额可用'}}</span>
                    </span>
                  </div>
                </div>
              </div>
              <div class="two">
                <img src="../../../assets/circular_grey.png">
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!-- 错误提示 -->
    <transition name="tip">
      <div class="lf-err-tip" v-if="isPayErr">付款金额有误</div>
      <div class="lf-err-tip" v-if="isBalanceErr">会员余额不足</div>
    </transition>
  </div>
</template>

<script>
  import {
    getMchInfo,
    getMemberInfoAndCoupons,
    getMemberPay,
    getWxPay
  } from '../../../api/vueAPI.js'
  import Keyboard from '../../../components/keyboard'

  export default {
    components: {
      Keyboard
    },
    data() { //页面所需数据集合
      return {
        htmlProportion: '', //屏幕长宽比例
        htmlWidth: '', //屏幕宽
        htmlheight: '', //屏幕高
        yenSymbol: '\u00A5', //金钱符号
        isPayErr: false, //付款金额有误错误提示框显示
        isBalanceErr: false, //会员余额不足错误提示框显示

        //自定义软键盘 数据对象
        keyboardOption: {
          show: false, //是否显示
          value: "" //金额
        },
        keyboardOptionvalue: "",
        zhifu1: 0,//判断是否正在输入金额

        isAllowModifyAmount: true, //是否允许修改金额

        isWxPay: true,
        payAmount: 0, //支付金额
        actualAmount: 0, //还需支付金额
        wxRemark: '', // 微信支付，微信备注
        isMember: false, //会员判断
        isMemberRadio: false, //是否会员支付

        isShowCoupon: false, //优惠券列表模态框显示
        isShowSelection: true, //是否不使用优惠券

        couponList: [], //所有优惠券
        canUseCouponList: [], //可使用优惠券列表
        canNotUseCouponList: [], //不可使用优惠券列表

        selectedCoupon: null, //选中的优惠券对象

        memberBalance: 0, //会员账户余额
        mchId: '', //商户Id
        wxOpenId: '', //微信OpenId
        operatorId: '', //操作员ID
        storeId: '', //门店ID
        tradeOrderId: '', //交易订单号
        toNewMemberUrl: '', //会员开卡地址
        merchantName: ' -- ', //商户名称
        headSculpture: require("../../../assets/MoRenTouXiang.png"), //默认头像
        money_shuru: false //判断输入状态
      }
    },

    watch: { //一个对象，键是需要观察的表达式，值是对应回调函数
      isPayErr(val, oldVal) { //监听[isPayErr]属性发生变化
        if (val === true) {
          setTimeout(() => {
            this.isPayErr = false;
          }, 1500)
        }
      },
      isBalanceErr(val, oldVal) {
        if (val === true) {
          setTimeout(() => {
            this.isBalanceErr = false;
          }, 1500)
        }
      },
      actualAmount(val, oldVal) {
        if (parseFloat(this.actualAmount) < 0) {
          this.actualAmount = '0'
        }
      },
      'keyboardOption.value': {
        deep: true,
        handler() {
          if(this.keyboardOption.value>1000000){
            alert("金额不能超过一百万")
            var value1=this.keyboardOption.value+"";
            var moneyLength=value1.length;
            this.keyboardOption.value=value1.substring(0, moneyLength - 1 );
          }
          this.keyboardClose()
        }
      },
      'keyboardOption.show': {
        deep: true,
        handler() {
          // this.keyboardClose()
          if (!this.keyboardOption.show) {
            this.money_shuru = false
          } else {
            this.money_shuru = true
          }
        }
      }
    },
    methods: { //vue函数集合
      // 判断是否能输入而自动弹出键盘
      judge_shuru() {
        if (this.isAllowModifyAmount) {
          this.showKeyboard();
          this.money_shuru = true;
        }
      },

      showKeyboard() {
        this.keyboardOption.show = true;

      },
      keyboardClose() { //键盘关闭 回调事件
        if (this.keyboardOption.value == '') { //未输入任何数字
          this.payAmount = 0;
        } else {
          this.payAmount = this.keyboardOption.value * 100; //payAmount 单位为：分
        }

        //如果选择了优惠券 ，校验优惠券最低消费金额是否满足
        if (this.selectedCoupon) {
          if (this.selectedCoupon.payAmountLimit > this.payAmount) {
            this.selectedCoupon = null;
          }
        }

        //重新计算还需支付金额
        this.actualPayMoney(); //计算还需支付金额
        this.reloadCouponList(); //重新加载优惠券列表

      },


      jumpAddMember() { /* 页面跳转-加入会员 */
        let thisHref = encodeURIComponent(window.location.href); //当前页面URL 并转码
        let toHref = this.toNewMemberUrl + "?mchId=" + this.mchId + "&openId=" + this.wxOpenId + "&returnURL=" +
          thisHref;
        window.location.href = toHref;
      },

      /* 付款确认框 */
      payConfirmModal() {
        //确认付款
        if (isNaN(this.payAmount) || !/^[1-9]\d*$/.test((this.payAmount + ""))) {
          this.isPayErr = true;
          return false;
        }

        //会员储值卡支付
        if (this.isMemberRadio && this.isMember) {
          if (this.memberBalance >= this.actualAmount) { //会员余额 >= 还需支付金额
            this.memberPayment(); //调起会员支付接口
          } else {
            this.isBalanceErr = true;
          }
          return;
        }

        let that = this;
        let couponNo = this.selectedCoupon ? this.selectedCoupon.couponNo : "";
        //微信支付：
        getWxPay(this.wxOpenId, this.payAmount, couponNo, this.storeId, this.operatorId, this.mchId, this.tradeOrderId,
          this.payToken).then(res => {

          if (typeof WeixinJSBridge == "undefined") {
            if (document.addEventListener) {
              document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
            } else if (document.attachEvent) {
              document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
              document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
            }
          }

          that.onBridgeReady(res.data);
        }).catch(err => {
        });
      },
      /* 会员储值支付 */
      memberPayment() {
        let that = this;
        let couponNo = this.selectedCoupon ? this.selectedCoupon.couponNo : "";
        getMemberPay(this.wxOpenId, this.payAmount, couponNo, this.storeId, this.operatorId, this.mchId, this.tradeOrderId,
          this.payToken).then(res => {
          //会员支付成功， 跳转页面
          that.$router.push({
            path: "/paySuccess",
            query: {
              orderAmount: res.data.orderAmount,
              discountAmount: res.data.discountAmount,
              amount: res.data.amount,
              tradeOrderId: res.data.tradeOrderId,
              payType: 0
            }
          })

        }).catch(err => {})
      },
      reloadCouponList() { //重新加载优惠券列表， 包含[可用优惠券] 和 [不可用优惠券]
        /* 处理优惠券 */
        this.canUseCouponList = [];
        this.canNotUseCouponList = [];
        for (let coupon of this.couponList) {
          if (coupon.payAmountLimit <= this.payAmount) {
            this.canUseCouponList.push(coupon);
          } else {
            this.canNotUseCouponList.push(coupon);
          }
        }

        if (this.selectedCoupon) { //包含选中的优惠券
          for (let key in this.canUseCouponList) {
            if (this.canUseCouponList[key].couponNo === this.selectedCoupon.couponNo) {
              this.canUseCouponList[key].selectStatus = true
            } else {
              this.canUseCouponList[key].selectStatus = false
            }
          }

        }
      },

      /* 打开优惠券模态框 */
      showCoupon() {
        if (!this.isMember) {
          return;
        }

        if (!this.couponList) { //无任何优惠券
          return;
        }

        this.reloadCouponList(); //重新加载优惠券列表
        this.isShowCoupon = true; //显示优惠券列表弹层

      },
      /* 关闭优惠券模态框 */
      closeCoupon() {
        //计算还需支付金额
        this.actualPayMoney();
        this.isShowCoupon = false; //关闭优惠券列表
      },
      /* 改变优惠券选择 */
      changeSelection(key) {
        switch (parseInt(key)) {
          case -1: //选择不使用优惠券
            this.isShowSelection = true
            for (let i in this.canUseCouponList) {
              this.canUseCouponList[i].selectStatus = false
            }
            this.selectedCoupon = null;
            break;
          default:
            this.isShowSelection = false
            for (let i in this.canUseCouponList) {
              this.canUseCouponList[i].selectStatus = false
            }
            this.canUseCouponList[key].selectStatus = true
            this.selectedCoupon = this.canUseCouponList[key]
            break;
        }
      },
      /* 改变会员支付方式 */
      changeMemberPayWay(boolean) {
        if (boolean) {
          this.isMemberRadio = false;
        } else {
          this.isMemberRadio = true;
        }
      },
      /* 计算还需支付金额 */
      actualPayMoney() {
        if (this.selectedCoupon) { //选择了优惠券
          this.actualAmount = this.payAmount - this.selectedCoupon.couponAmount;
          console.log(this.selectedCoupon);
        } else {
          this.actualAmount = this.payAmount; //等于录入金额
        }
      },
      /* 唤醒微信支付*/
      onBridgeReady(resDataObj) {

        let payObj = JSON.parse(resDataObj.payParam);
        let that = this;

        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId": payObj.appId, //公众号名称，由商户传入
            "timeStamp": payObj.timeStamp, //时间戳，自1970年以来的秒数
            "nonceStr": payObj.nonceStr, //随机串
            "package": payObj.package,
            "signType": payObj.signType, //微信签名方式：
            "paySign": payObj.paySign, //微信签名
            "prepayId": payObj.prepayId
          },
          function(res) {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              // 使用以上方式判断前端返回,微信团队郑重提示：
              //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
              that.$router.push({
                path: "/paySuccess",
                query: {
                  orderAmount: resDataObj.orderAmount,
                  discountAmount: resDataObj.discountAmount,
                  amount: resDataObj.amount,
                  tradeOrderId: resDataObj.tradeOrderId,
                  payType: 1
                }
              })

            }
            if (res.err_msg == "get_brand_wcpay_request:cancel") {
              alert("支付取消");
            }
            if (res.err_msg == "get_brand_wcpay_request:fail") {
              alert('支付失败')
              alert(JSON.stringify(res))
            }
            if (res.err_msg == "total_fee") {
              alert('缺少参数')
            }
          }
        );
      },

      //点击 【确认】 回调事件
      toPayFunc(){

        this.$nextTick(() => {
          this.payConfirmModal();
        });

      }
    },
    mounted(){
      this.judge_shuru();
      },
    created() { //vue 生命周期 - 在实例创建完成后被立即调用

      //1. 获取当面屏幕尺寸
      this.htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
      this.htmlheight = document.documentElement.clientHeight || document.body.clientHeight;
      this.htmlProportion = this.htmlWidth / 750;

      //2. 获取url参数
      //wxOpenId(必填)
      let wxOpenId = this.$route.query.wxOpenId;
      if (!wxOpenId) return alert('参数有误');
      this.wxOpenId = wxOpenId;

      //获取mchId信息(必填)
      let mchId = this.$route.query.mchId;
      if (!mchId) return alert('参数有误');
      this.mchId = mchId;

      //获取toNewMemberUrl信息(必填)
      let toNewMemberUrl = this.$route.query.toNewMemberUrl;
      if (!toNewMemberUrl) return alert('参数有误');
      this.toNewMemberUrl = toNewMemberUrl;

      //获取payToken信息(必填)
      let payToken = this.$route.query.payToken;
      if (!payToken) return alert('参数有误');
      this.payToken = payToken;

      //店铺ID（非必填）
      let storeId = this.$route.query.storeId;
      if (storeId) {
        this.storeId = storeId
      }

      //操作员ID（非必填）
      let operatorId = this.$route.query.operatorId;
      if (operatorId) {
        this.operatorId = operatorId;
      }
      //金额（非必填） 单位： 分
      let payAmount = this.$route.query.payAmount;
      if (payAmount) {
        this.payAmount = payAmount;
        this.keyboardOption.value = payAmount / 100;
        this.actualPayMoney(); //计算还需支付金额
        this.isAllowModifyAmount = false; //不允许修改金额
      } else {
        this.isAllowModifyAmount = true; //允许修改金额
      }

      let tradeOrderId = this.$route.query.tradeOrderId; //订单编号
      if (tradeOrderId) {
        this.tradeOrderId = tradeOrderId;
      }


      let that = this;
      //接口查询会员优惠券列表
      getMemberInfoAndCoupons(this.payToken, this.mchId, this.storeId, this.wxOpenId).then(res => {
        if (res.data.memberId) { //会员
          that.isMember = true;
          that.memberBalance = res.data.balance;
          res.data.receiveCouponList.forEach(item => {
            if (item.usableFlag) {
              that.couponList.push(item);
            }
          });
        }
      }).catch(err => {});
      //异步获取商户名称
      getMchInfo(this.payToken, this.mchId, this.storeId, this.operatorId).then(res => {
        this.merchantName = res.data.mchName;
        this.merchantImg = res.data.merchantImg;

      }).catch(error => {});

    },
  }
</script>

<style scoped>
  @import url("index.css");

</style>



